<template>
  <div>
    <page-header></page-header>
    <div class="globalWidth">
      <div class="wrapper">
        <h2 class="top-title">
          
        </h2>
        <el-row :gutter="20">
          <el-col :xs="24" :sm="24">
            <div class="img">
              <img src="static/images/about-banner.jpg" alt="">
            </div>
          </el-col>
          <el-col :xs="24" :sm="12"><div class="img"><img src="static/images/about-left.jpg" alt=""></div></el-col>
          <el-col :xs="24" :sm="12">
            <div class="word">
              <div class="intro">
                2017129
              </div>
              <div class="intro">
                 ++10
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <page-footer></page-footer>
  </div>
</template>
<script>
  import PageHeader from '../components/Header'
  import PageFooter from '../components/Footer'
  export default {
    data() {
      return {}
    },
    components: {
      PageHeader,
      PageFooter
    },
    methods: {}
  }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
  .wrapper {
    .top-title {
      padding: 70px 0 20px;
      text-align: center;
      font-family: "Microsoft YaHei";
      font-size: 24px;
      font-weight: lighter;
      border-bottom: 1px solid #DCDFE6;
      margin-bottom: 20px;
    }
    .img {
      margin-bottom: 20px;
      img {
        width: 100%;
      }
    }
    .word {
      .intro {
        font-family: "Microsoft YaHei";
        font-size: 16px;
        color: #475669;
        line-height: 1.5;
        text-indent: 2em;
        padding: 0 10px;
        margin-bottom: 40px;
        margin-top: 10px;
      }
    }
  }
  @media screen and (min-width: 768px) {
    .wrapper {
      .top-title {
        text-align: left;
        padding: 70px 0 20px 50px;
        margin-bottom: 40px;
      }
      .word {
        .intro {
          margin-top: 50px;
        }
      }
    }
  }
  @media screen and (min-width: 1200px) {
    .wrapper {
      .top-title {
        padding: 70px 0 20px;
      }
    }
  }
</style>



